<template>
    <div class="rank">
        <h1>热门作者</h1>
        <ul>
            <li v-for="item in authorLists" :key="item.id" @click="getAuthor(item.user_id, 0); showAuthorDetail=true">
                <img :src="item.web_url" alt="">
                <div class="name">
                    <h3>{{item.user_name}}</h3>
                    <p>{{item.desc}}</p>
                </div>
                <span v-show="!authorFansID.map(o=>o).includes(item.user_id)"
                    @click.stop="addAuthorFans({id:item.user_id,list:item})">关注</span>
                <span class="focused" v-show="authorFansID.map(o=>o).includes(item.user_id)"
                    @click.stop="removeAuthorFans({id:item.user_id})">已关注</span>
            </li>
        </ul>
        <AuthorDetails :authorIntroduce="authorIntroduce" v-if="showAuthorDetail"
            @close-details="showAuthorDetail = false">
        </AuthorDetails>
    </div>
</template>

<script>
import { getAuthorDetails } from "@/apis";
import AuthorDetails from '@/components/AuthorDetails.vue';
import { mapState, mapMutations } from "vuex";
export default {
    props: {
        authorLists: Array,
    },
    components: {
        AuthorDetails,
    },
    data() {
        return {
            authorIntroduce: [],
            showAuthorDetail: false,
        };
    },
    computed: {
        ...mapState(['authorFansID', 'authorFansList'])
    },
    methods: {
        getAuthor: function (id, page) {
            getAuthorDetails(id, page).then(res => {
                // console.log(res.data.data);
                this.authorIntroduce = res.data.data;
            });
            // console.log(id);
        },
        ...mapMutations(['addAuthorFans', 'removeAuthorFans'])
    },
    watch: {
        
    }
};
</script>

<style lang="less" scoped>
.rank {
    width: 100%;

    h1 {
        font-size: 18rem;
        font-weight: normal;
        margin-bottom: 20rem;
    }

    ul {
        width: 100%;

        li {
            width: 100%;
            display: flex;
            align-items: center;
            margin-bottom: 25rem;
            position: relative;

            img {
                width: 50rem;
                height: 50rem;
                background-size: cover;
                border-radius: 50%;
            }

            .name {
                width: 218rem;
                margin-left: 10rem;

                h3 {
                    font-weight: normal;
                    font-size: 16rem;
                    margin: 0;
                    padding: 0;
                    margin-bottom: 4rem;
                }

                p {
                    width: 100%;
                    color: rgb(168, 167, 167);
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                    margin: 0;
                    padding: 0;
                }
            }

            span {
                display: block;
                padding: 2rem 10rem 2rem 10rem;
                border: 1rem solid black;
                border-radius: 20rem;
                font-size: 13rem;
                white-space: nowrap;
                position: absolute;
                top: 20rem;
                right: 0;
            }
        }
    }

    .focused {
            // background-color: rgba(161, 161, 161, 0.603);
            border-color: #a5a2a2;
        }
}
</style>
