<template>
    <div class="diary">
        <ul>
            <li v-for="(item) in diaryLists" :key="item.id">
                <img v-if="diaryLists.length" :src="item.img_url" alt="">
                <van-image v-else width="100" height="100" src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg" />
                <div class="like">
                    <i v-show="!diaryLikedID.map(o => o.id).includes(item.id)"
                        @click="addDiaryLikedID({id:item.id})"></i>
                    <i v-show="diaryLikedID.map(o => o.id).includes(item.id)"
                        @click="removeDiaryLikedID({id:item.id})"></i>
                </div>
                <h3>{{item.content}}</h3>
                <div class="user">
                    <p>{{item.user.user_name}}</p>
                    <span>{{item.input_date.split(' ')[0].split('-').join('.')}}</span>
                </div>
            </li>
        </ul>
        <div class="add"></div>
    </div>
</template>

<script>
export default {
    props: {
        diaryLists: Array,
    },
    data() {
        return {
            isLiked: false,
            diaryLikedID: JSON.parse(localStorage.getItem('diaryLikedID')) || [],
        };
    },
    methods: {
        addDiaryLikedID(id) {
            this.diaryLikedID = [...this.diaryLikedID, id];
        },
        removeDiaryLikedID(id) {
            this.diaryLikedID = this.diaryLikedID.filter(o => o.id !== id.id);
        },
    },
    watch: {
        diaryLikedID: function (diaryLikedID) {
            localStorage.setItem('diaryLikedID', JSON.stringify(diaryLikedID));
        },
    }
};
</script>

<style lang="less" scoped>
.diary {
    width: 100%;

    ul {
        width: 100%;
        margin-left: 1rem;
        margin-bottom: 20rem;

        column-count: 2;
        /*css3新增，把contaner容器中的内容分为三列*/
        column-gap: 10px;
        /*定义列之间的间隙为20px*/

        li {
            width: 163rem;
            position: relative;
            box-shadow: 0 0 3rem 1rem #ccc;
            border-radius: 5rem;
            margin-bottom: 20rem;

            -webkit-column-break-inside: avoid;
            break-inside: avoid;
            counter-increment: item-counter;

            img {
                width: 100%;
                background-size: cover;
                border-top-left-radius: 5rem;
                border-top-right-radius: 5rem;
            }

            .like {
                width: 20rem;
                height: 20rem;
                background-color: rgb(3 3 3 / 7%);
                position: absolute;
                top: 10rem;
                right: 10rem;
                border-radius: 50%;

                i {
                    position: absolute;
                    top: 5rem;
                    left: 4rem;
                    width: 12rem;
                    height: 10rem;
                    background-size: cover;

                    &:nth-child(1) {
                        background-image: url(../assets/discover_laud_unselected.png);
                    }

                    &:nth-child(2) {
                        background-image: url(../assets/discover_laud_selected.png);
                    }
                }
            }

            h3 {
                font-weight: normal;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                padding: 0rem 11rem 0 6rem;
                min-height: 30rem;
                line-height: 1.2;
                font-size: 14rem;
            }

            .user {
                padding-left: 10rem;
                padding-bottom: 20rem;
                color: rgb(147, 147, 147);

                p {
                    padding-bottom: 1\5rem;
                }
            }
        }
    }

    .add {
        width: 50rem;
        height: 50rem;
        background-image: url(../assets/diary_add_icon.png);
        background-size: cover;
        position: fixed;
        bottom: 80rem;
        left: 50%;
        margin-left: -25rem;
    }
}
</style>
