<template>
    <div class="details">
        <div class="back" @click="$emit('close-details')"></div>
        <div class="d_intro">
            <img :src="authorIntroduce[0]?.author.web_url" alt="">
            <h3>{{authorIntroduce[0]?.author.user_name}}</h3>
            <p class="summary" v-if="authorIntroduce[0]?.author.summary !== authorIntroduce[0]?.author.desc">
                {{authorIntroduce[0]?.author.summary}}</p>
            <p class="desc">{{authorIntroduce[0]?.author.desc}}</p>
            <span class="fans" v-show="!authorFansID.map(o=>o).includes(authorIntroduce[0]?.author.user_id)"
                @click.stop="addAuthorFans({id:authorIntroduce[0]?.author.user_id,list:authorIntroduce[0]?.author})">关注</span>
            <span class="fans" v-show="authorFansID.map(o=>o).includes(authorIntroduce[0]?.author.user_id)"
                @click.stop="removeAuthorFans({id:authorIntroduce[0]?.author.user_id})">已关注</span>
            <div class="fans_total">
                <span>{{authorIntroduce[0]?.author.fans_total}}关注</span>
            </div>
        </div>
        <ul>
            <li v-for="item in authorIntroduce" :key="item.id" @click="$router.push({path: '/article', query: {type:item.category, articleId: item.content_id}})">
                <h6>- {{item.share_list.wx.title.split('|')[0]}} -</h6>
                <div class="works">
                    <h3>{{item.title}}</h3>
                    <p v-if="item.answerer">{{item.answerer.user_name}}答</p>
                    <p v-else>文/{{item.author.user_name}}</p>
                    <h4>{{item.forward}}</h4>
                    <img :src="item.img_url" alt="">
                    <div class="btn">
                        <div class="liked">
                            <i v-show="!articleLikedID.map(o => o).includes(item.content_id)"
                                @click.stop="addArticleLikedID({id:item.content_id})"></i>
                            <i v-show="articleLikedID.map(o => o).includes(item.content_id)"
                                @click.stop="removeArticleLikedID({id:item.content_id})"></i>
                            <span>{{!articleLikedID.map(o =>
                            o).includes(item.content_id)?item.like_count:item.like_count+1}}</span>
                        </div>
                        <div class="repost" @click.stop="isShowShare"></div>
                    </div>
                </div>
            </li>
        </ul>
        <transition name="share">
            <div class="share_type" v-show="isShare">
                <div class="share_close" @click="closeShare"></div>
                <div class="type">
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
    props: {
        authorIntroduce: Array,
    },
    data() {
        return {

        };
    },
    computed: {
        ...mapState(['isShare', 'authorFansID', 'authorFansList', 'articleLikedID'])
    },
    methods: {
        ...mapMutations(['isShowShare', 'closeShare', 'addAuthorFans', 'removeAuthorFans', 'addArticleLikedID', 'removeArticleLikedID'])
    }
};
</script>

