<template>
    <div class="rank">
        <ul>
            <li v-for="item in rankLists" :key="item.id"
                @click="getRankDetail(item.id, item.title); showRankDetails=true">
                <div class="title">
                    <h1>{{item.title}}</h1>
                    <div class="all">
                        <span>完整榜单</span>
                        <i></i>
                    </div>
                </div>
                <div class="content">
                    <img v-if="item.img_url" :src="item.img_url" alt="">
                    <van-image v-else width="60" height="60" src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg" />
                    <div class="rank_box" v-if="item.contents">
                        <div class="ranking" v-for="(rank, i) in item.contents" :key="i" @click.stop="$router.push({path: '/article', query: {type:rank.category, articleId: rank.id}})">
                            <span class="paiming">{{i + 1}}.</span>
                            <span>{{rank.title}}</span>
                        </div>
                    </div>
                    <van-skeleton v-else title :row="5" />
                </div>
            </li>
        </ul>
        <div class="detailsLists" v-if="showRankDetails">
            <div class="top">
                <i @click="showRankDetails=false"></i>
                <span>{{title}}</span>
            </div>
            <RankDetails :detailLists="detailLists"></RankDetails>
        </div>

        <div class="webView" v-if="showLoading">
            <div class="loading">
                <img v-for="i of 29" :key="i" :src="`/ani_loading/webview_loading_${i>9?i:'0'+i}.png`" alt=""
                    :style="`animation-delay: ${0.1*(i)+'s'};`">
            </div>
        </div>
    </div>
</template>

<script>
import RankDetails from '@/components/RankDetails.vue';
import { getFoundRankDetail } from "@/apis";
import { mapMutations, mapState } from 'vuex';
export default {
    props: {
        rankLists: Array,
    },
    components: {
        RankDetails
    },
    data() {
        return {
            detailLists: [],
            title: null,
            showRankDetails: false
        };
    },
    computed: {
        ...mapState(['showLoading'])
    },
    methods: {
        getRankDetail: function (id, title) {
            // this.isShowLoading()
            getFoundRankDetail(id).then(res => {
                this.detailLists = res.data.data;
                // this.closeshowLoading();
            });
            this.title = title;
        },
        ...mapMutations(['isShowLoading', 'closeshowLoading'])
    },
};
</script>

<style lang="less" scoped>
@keyframes loading {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.webView {
    width: 100%;
    height: 100vh;
    background-color: white;
    position: relative;
    z-index: 999;

    .loading {
        width: 80rem;
        height: 80rem;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -40rem;
        margin-top: -40rem;

        img {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-size: cover;
            animation: loading .5s linear infinite;
        }
    }
}

.rank {
    width: 100%;
    height: 100%;

    ul {

        li {
            margin-bottom: 30rem;

            .title {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 5rem;

                h1 {
                    font-size: 16rem;
                    font-weight: normal;
                }

                .all {
                    width: 65rem;
                    font-size: 12rem;
                    color: #ccc;
                    display: flex;
                    align-items: center;

                    i {
                        display: inline-block;
                        width: 12rem;
                        height: 12rem;
                        background-image: url(../assets/arrow_right.png);
                        background-size: cover;
                        margin-top: 2rem;
                    }
                }
            }

            .content {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                img {
                    width: 90rem;
                    height: 90rem;
                    border-radius: 5rem;
                    background-size: cover;
                }

                .rank_box {
                    flex-grow: 1;
                    margin-left: 15rem;
                }

                .ranking {
                    font-size: 14rem;
                    margin-bottom: 10rem;
                    display: flex;
                    align-items: center;

                    span {
                        color: #8a8888;

                        &:nth-child(2) {
                            display: inline-block;
                            width: 90%;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            word-break: normal;
                            margin-bottom: 2rem;
                        }
                    }

                    &:nth-child(1) {

                        span {

                            &:nth-child(1) {
                                color: red;
                            }
                        }
                    }

                    &:nth-child(2) {

                        span {

                            &:nth-child(1) {
                                color: #ff6d6d;
                            }
                        }
                    }

                    &:nth-child(3) {

                        span {

                            &:nth-child(1) {
                                color: #feb840;
                            }
                        }
                    }
                }
            }
        }
    }

}

.detailsLists {
    width: 375rem;
    height: 100vh;
    overflow: auto;
    background-color: aliceblue;
    position: absolute;
    top: 0;
    left: 0;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        -webkit-appearance: none;
        background: transparent;
    }

    .top {
        width: 100%;
        position: relative;
        margin-top: 10rem;
        margin-left: 10rem;

        i {
            position: absolute;
            top: -2px;
            left: 10px;
            width: 18rem;
            height: 25rem;
            background-image: url(../assets/ali_feedback_common_back_btn_pressed.png);
            background-size: cover;
        }

        span {
            font-size: 14rem;
            margin-left: 35rem;
        }
    }
}
</style>
