<template>
    <div class="radio">
        <div class="date_list" v-if="showDate">
            <van-datetime-picker class="d_List" v-model="currentDate" type="year-month" title="选择日期" :min-date="minDate"
                :max-date="maxDate" :formatter="formatter" @cancel="showDate=false" />
            <!-- @confirm="changeDate(val)" -->
            <div class="date_list_mask" @click="showDate=false"></div>
        </div>
        <span class="o_dates" v-show="!showDate" @click="showDate=true">2022年10月 <span class="down"></span></span>
        <h1>10月</h1>
        <ul>
            <li v-for="item in radioLists" :key="item.id" @click="$router.push({path:'/article',query:{type:item.category, articleId:item.id}});getAudioSrc({src: item.audio_url, audio: item})
            ">
                <!-- getAudioSrc({src: item.audio_url, audio: item}) -->
                <img :src="item.cover" alt="">
                <div class="info">ONE电台 <span>{{item.volume}}</span></div>
                <p>{{item.title}}</p>
                <div class="play_pause">
                    <div class="play" v-if="audioID != item.id" @click.stop="change_A_id({id:item.id}),getAudioSrc({src:item.audio_url, audio: item}),audioPlay(), $emit('audio-play')"></div>
                    <div class="pause" v-else @click.stop="pause_A_id(),audioPause(), $emit('audio-pause')"></div>
                </div>
                <div class="author">
                    <div class="name">
                        <img :src="item.author.web_url" alt="">
                        <span>{{item.author.user_name}}</span>
                    </div>
                    <div class="btn">
                        <div class="laud">
                            <i v-show="!articleLikedID.map(o => o).includes(item.id)"
                                @click.stop="addArticleLikedID({id:item.id})"></i>
                            <i v-show="articleLikedID.map(o => o).includes(item.id)"
                                @click.stop="removeArticleLikedID({id:item.id})"></i>
                            <span>{{!articleLikedID.map(o => o).includes(item.id) ? item.like_count : item.like_count +
                            1}}</span>
                        </div>
                        <div class="repost"></div>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
    props: {
        radioLists: Array
    },
    data() {
        return {
            isLiked: false,
            showDate: false,
            currentDate: new Date(),
            minDate: new Date(2012, 0, 1),
            maxDate: new Date(2022, 9, 1),
        };
    },
    computed: {
        ...mapState(['articleLikedID', 'playing', 'audio', 'audioID'])
    },
    methods: {
        ...mapMutations(['addArticleLikedID', 'removeArticleLikedID', 'audioPlay', 'audioPause', 'change_A_id', 'pause_A_id', 'getAudioSrc']),
        formatter: function (type, val) {
            if (type === 'year') {
                return `${val}年`;
            }
            if (type === 'month') {
                return `${val}月`;
            }
            console.log(val);
            return val;
        },
    }
};
</script>

<style lang="less" scoped>
.date_list {
    width: 375rem;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;

    .d_List {
        width: 100%;
        height: 200rem;
        background-color: white;
        overflow: auto;
    }

    .date_list_mask {
        width: 100%;
        // height: calc(100vh -200rem);
        height: 467rem;
        background-color: rgba(0, 0, 0, 0.3);
    }
}

.o_dates {
    display: block;
    width: 100%;
    text-align: center;
    color: gray;
    position: relative;

    .down {
        display: inline-block;
        width: 15rem;
        height: 15rem;
        background-image: url(../assets/arrow_down_black.png);
        background-size: cover;
        position: absolute;
        top: 1rem;
        right: 120rem;
    }
}

.radio {
    width: 100%;

    h1 {
        font-weight: normal;
        color: #ccc;
        width: 100%;
        text-align: center;
        margin-top: 10rem;
        margin-bottom: 20rem;
    }

    ul {
        width: 100%;

        li {
            width: 100%;
            position: relative;
            margin-bottom: 35rem;

            img {
                width: 100%;
                border-radius: 10rem;
                background-size: cover;
            }

            .info {
                position: absolute;
                top: 15rem;
                left: 15rem;
                font-size: 12rem;
                color: white;

                span {
                    color: rgb(159, 158, 158);
                }
            }

            p {
                width: 280rem;
                margin-left: 15rem;
                font-size: 17rem;
                color: #e5e3e3;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                word-break: normal;
                position: absolute;
                bottom: 35rem;
                left: 0;
            }

            .play_pause {
                position: absolute;
                bottom: 45rem;
                right: 20rem;
                width: 30rem;
                height: 30rem;

                &>div {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background-size: cover;
                }

                .play {
                    background-image: url(../assets/feeds_radio_play.png);
                }

                .pause {
                    background-image: url(../assets/feeds_radio_pause.png);
                }
            }

            .author {
                width: 95%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 15rem;
                margin-left: 5rem;

                .name {
                    display: flex;
                    align-items: center;

                    img {
                        width: 20rem;
                        height: 20rem;
                        background-size: cover;
                        border-radius: 50%;
                    }

                    span {
                        color: #ccc;
                        margin-left: 5rem;
                    }
                }

                .btn {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .laud {
                        margin-right: 50rem;
                        position: relative;

                        i {
                            position: absolute;
                            top: -2rem;
                            left: 0;
                            width: 22rem;
                            height: 20rem;
                            background-size: cover;

                            &:nth-child(1) {
                                background-image: url(../assets/discover_laud_unselected.png);
                            }

                            &:nth-child(2) {
                                background-image: url(../assets/discover_laud_selected.png);
                            }
                        }

                        span {
                            margin-left: 30rem;
                            color: #ccc;
                        }
                    }

                    .repost {
                        width: 22rem;
                        height: 20rem;
                        background-image: url(../assets/discover_repost.png);
                        background-size: cover;
                    }
                }
            }
        }
    }
}
</style>
