
<template>
  <div class="found">
    <div class="type">
      <ul :style="`margin-left: (${-typeIndex} * 85)rem`">
        <li v-for="(ft, i) in foundTypes" :key="i" @click="typeIndex = i; getArticles(ft.category)"
          :class="{active: typeIndex===i}">{{ft.name}}
        </li>
      </ul>
      <div class="f_search" @click="$router.push({path: '/search'})"></div>
      <div class="f_content">
        <keep-alive>
          <FoundRead v-if="read===typeIndex" :articleLists="articleLists"></FoundRead>
          <FoundGraphic v-if="graphic===typeIndex" :graphicLists="graphicLists"></FoundGraphic>
          <FoundProjectList v-if="project===typeIndex" :foundProjectList="foundProjectList"></FoundProjectList>
          <FoundRank v-if="rank===typeIndex" :rankLists="rankLists"></FoundRank>
          <FoundQuestion v-if="question===typeIndex" :articleLists="articleLists"></FoundQuestion>
          <FoundMusic v-if="music===typeIndex" :musicLists="musicLists"></FoundMusic>
          <FoundMovie v-if="movie===typeIndex" :movieLists="movieLists"></FoundMovie>
          <FoundRadio v-if="radio===typeIndex" :radioLists="radioLists" @audio-play="$emit('audio-play')"
            @audio-pause="$emit('audio-pause')"></FoundRadio>
          <FoundDiary v-if="diary===typeIndex" :diaryLists="diaryLists"></FoundDiary>
          <FoundAuthor v-if="author===typeIndex" :authorLists="authorLists"></FoundAuthor>
        </keep-alive>
      </div>
    </div>

    <div class="locadingmask" v-if="showLoading">
      <div class="locading"></div>
    </div>

    <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="(item, i) in graphicLists" :key="i" :title="i" />
    </van-list> -->
  </div>
</template>

<script>
import { getFoundArticle, getFoundProject, getFoundRank, getFoundDiary, getFoundAuthor } from "@/apis";
import FoundRead from '@/components/FoundRead.vue';
import FoundGraphic from '@/components/FoundGraphic.vue';
import FoundProjectList from '@/components/FoundProjectList.vue';
import FoundRank from '@/components/FoundRank.vue';
import FoundQuestion from '@/components/FoundQuestion.vue';
import FoundMusic from '@/components/FoundMusic.vue';
import FoundMovie from '@/components/FoundMovie.vue';
import FoundRadio from '@/components/FoundRadio.vue';
import FoundDiary from '@/components/FoundDiary.vue';
import FoundAuthor from '@/components/FoundAuthor.vue';
import { mapMutations, mapState } from "vuex";
// import { ref } from "vue";
// import { mapMutations, mapState } from "vuex";
export default {
  props: {},
  components: {
    FoundProjectList,
    FoundRead,
    FoundGraphic,
    FoundRank,
    FoundQuestion,
    FoundMusic,
    FoundMovie,
    FoundRadio,
    FoundDiary,
    FoundAuthor,
  },
  data() {
    return {
      // typeIndex: JSON.parse(localStorage.getItem('typeIndex')) || 2,
      typeIndex: 2,
      foundTypes: [
        {
          name: '图文',
          id: 1,
          category: 0,
        },
        {
          name: '阅读',
          id: 0,
          category: 1,
        },
        {
          name: '专题',
          id: 2,
          category: 'banner',
        },
        {
          name: '热榜',
          id: 3,
          category: 'rank',
        },
        {
          name: '问答',
          id: 4,
          category: 3,
        },
        {
          name: '音乐',
          id: 5,
          category: 4,
        },
        {
          name: '影视',
          id: 6,
          category: 5,
        },
        {
          name: '电台',
          id: 7,
          category: 'radio',
        },
        {
          name: '小记',
          id: 8,
          category: 'diary',
        },
        {
          name: '作者',
          id: 9,
          category: 'author',
        }
      ],
      graphic: 0,
      read: 1,
      project: 2,
      rank: 3,
      question: 4,
      music: 5,
      movie: 6,
      radio: 7,
      diary: 8,
      author: 9,
      foundProjectList: [],
      graphicLists: [],
      rankLists: [],
      radioLists: [],
      articleLists: [],
      authorLists: [],
      diaryLists: [],
      musicLists: [],
      movieLists: [],
      date: '2022-10',
      // loadMore: 0,
      // loading: false,
      // finished: false,
    };
  },
  computed: {
    ...mapState(['showLoading'])
  },
  methods: {
    getArticles: function (type) {
      switch (type) {
        case 0:
          this.isShowLoading();
          getFoundArticle(0, this.date).then(res => {
            // console.log(res.data.data);
            // this.graphicLists = res.data.data;
            this.graphicLists = [...this.graphicLists, ...res.data.data];
            // console.log(res);
            // this.loading = false
            this.closeshowLoading();
          });
          break;
        case 'banner':
          console.log('专题');
          break;
        case 'rank':
          this.isShowLoading();
          getFoundRank().then(res => {
            // console.log(res.data.data);
            this.rankLists = res.data.data;
            this.closeshowLoading();
          });
          break;
        case 'radio':
          this.isShowLoading();
          getFoundArticle(8).then(res => {
            // console.log(res.data.data);
            this.radioLists = res.data.data;
            this.closeshowLoading();
          });
          break;
        case 'diary':
          this.isShowLoading();
          getFoundDiary().then(res => {
            this.diaryLists = res.data.data.list;
            this.closeshowLoading();
          });
          break;
        case 'author':
          this.isShowLoading();
          getFoundAuthor().then(res => {
            this.authorLists = res.data.data;
            this.closeshowLoading();
          });
          break;
        case 4:
          this.isShowLoading();
          getFoundArticle(type, '2022-09').then(res => {
            this.musicLists = res.data.data;
            getFoundArticle(type, '2022-05').then(res => {
              this.musicLists = [...this.musicLists, ...res.data.data];
              getFoundArticle(type, '2022-03').then(res => {
                this.musicLists = [...this.musicLists, ...res.data.data];
                getFoundArticle(type, '2020-10').then(res => {
                  this.musicLists = [...this.musicLists, ...res.data.data];
                  getFoundArticle(type, '2020-03').then(res => {
                    this.musicLists = [...this.musicLists, ...res.data.data];
                    this.closeshowLoading();
                  });
                });

              });
            });
          });
          break;
        case 5:
          this.isShowLoading();
          getFoundArticle(type, '2022-08').then(res => {
            this.movieLists = res.data.data;
            getFoundArticle(type, '2022-07').then(res => {
              this.movieLists = [...this.movieLists, ...res.data.data];
              getFoundArticle(type, '2022-05').then(res => {
                this.movieLists = [...this.movieLists, ...res.data.data];
                getFoundArticle(type, '2022-03').then(res => {
                  this.movieLists = [...this.movieLists, ...res.data.data];
                  getFoundArticle(type, '2022-02').then(res => {
                    this.movieLists = [...this.movieLists, ...res.data.data];
                    this.closeshowLoading();
                  });
                });

              });
            });
          });
          break;
        default:
          this.isShowLoading();
          getFoundArticle(type).then(res => {
            this.articleLists = res.data.data;
            this.closeshowLoading();
          });
          break;
      }
    },
    ...mapMutations(['isShowLoading', 'closeshowLoading']),
  },
  // watch: {
  //   typeIndex:  function (n) {
  //     localStorage.setItem('typeIndex',n)
  //   }
  // },



  created() {
    this.isShowLoading();
    getFoundProject().then(res => {
      this.foundProjectList = res.data.data;
      this.closeshowLoading();
    });
  },


};
</script>

