<template>
    <div class="music">
        <div class="date" v-for="d in date" :key="d">
            <div class="date_list" v-if="showDate">
                <van-datetime-picker class="d_List" v-model="currentDate" type="year-month" title="选择日期"
                    :min-date="minDate" :max-date="maxDate" :formatter="formatter" @cancel="showDate=false" />
                <!-- @confirm="changeDate(val)" -->
                <div class="date_list_mask" @click="showDate=false"></div>
            </div>
            <span class="o_dates" v-show="!showDate" @click="showDate=true">2022年10月 <span class="down"></span></span>
            <p class="month">{{d}}月</p>
            <ul>
                <li v-for="(item, i) in musicLists" :key="i"
                    @click="$router.push({path: '/article', query: {type:item.category, articleId:item.id}})">
                    <img v-if="item.cover" :src="item.cover" alt="">
                    <van-image v-else width="60" height="60" src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg" />
                    <div class="info" v-if="item.title">
                        <h3>{{item.title}}</h3>
                        <p>{{item.subtitle}}</p>
                    </div>
                    <van-skeleton v-else title :row="3" />
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
    props: {
        musicLists: Array,
    },
    data() {
        return {
            date: ['09', '-05', '10', '03'],
            showDate: false,
            currentDate: new Date(),
            minDate: new Date(2012, 0, 1),
            maxDate: new Date(2022, 9, 1),
        };
    },
    methods: {
        ...mapMutations(['getAudioSrc']),
        formatter: function (type, val) {
            if (type === 'year') {
                return `${val}年`;
            }
            if (type === 'month') {
                return `${val}月`;
            }
            console.log(val);
            return val;
        },
    }
};
</script>

<style lang="less" scoped>
.date_list {
    width: 375rem;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;

    .d_List {
        width: 100%;
        height: 200rem;
        background-color: white;
        overflow: auto;
    }

    .date_list_mask {
        width: 100%;
        // height: calc(100vh -200rem);
        height: 467rem;
        background-color: rgba(0, 0, 0, 0.3);
    }
}

.o_dates {
    display: block;
    width: 100%;
    text-align: center;
    color: gray;
    position: relative;

    .down {
        display: inline-block;
        width: 15rem;
        height: 15rem;
        background-image: url(../assets/arrow_down_black.png);
        background-size: cover;
        position: absolute;
        top: 1rem;
        right: 120rem;
    }
}

.music {
    width: 100%;

    .month {
        width: 100%;
        text-align: center;
        color: #ccc;
        margin-bottom: 20rem;
        margin-top: 10rem;
    }

    ul {
        width: 100%;

        li {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20rem;

            img {
                width: 60rem;
                height: 60rem;
                border-radius: 10rem;
                background-size: cover;
            }

            .info {
                flex-grow: 1;
                margin-left: 15rem;

                h3 {
                    width: 260rem;
                    margin: 0;
                    padding: 0;
                    font-size: 16rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                    margin-bottom: 15rem;
                }

                p {
                    width: 260rem;
                    color: rgb(171, 169, 169);
                    margin-bottom: 8rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                }
            }
        }
    }
}
</style>
